<template>
  <div style="text-align: center;">
    <h2 style="min-height:600px; padding-top: 250px;">redirecting...</h2>
  </div>
</template>

<script>

  const dict = {
    "datadriven": "https://www.amazon.com/Data-Driven-Performance-Analytics-Extraordinary-ebook/dp/B00QQMV0XC",
    "businessanalytics": "https://www.amazon.com/Behind-Every-Good-Decision-Profitable/dp/0814449212",
    "badir": "https://aryng.com/aryng_BADIR_advantage",

  }

  export default {
    mounted() {
      const { key  } = this.$route.params;
      const   link   = dict[key];

      setTimeout(() => {
        if (!link) { return; }
        window.location = link;
      }, 3000);
    }
  }

</script>